// PathProperties.tsx

import React, { useState, useEffect, useRef } from 'react';
import { ArtboardViewModel } from '../../viewmodel/ArtboardViewModel';
import { Path } from '../../lib/geometry/Path';
import { CapType } from '../../lib/geometry/CapType';
import { CornerType } from '../../lib/geometry/CornerType';

interface PathPropertiesProps {
  artboardViewModel: ArtboardViewModel;
  selectedPath: Path;
}

const PathProperties: React.FC<PathPropertiesProps> = ({ artboardViewModel, selectedPath }) => {
  // Local state variables
  const [localStrokeWidth, setLocalStrokeWidth] = useState<string>(
    selectedPath.strokeWidth.toString(),
  );
  const [localFillOpacity, setLocalFillOpacity] = useState<string>(
    selectedPath.fillOpacity.toString(),
  );
  const [localStrokeOpacity, setLocalStrokeOpacity] = useState<string>(
    selectedPath.strokeOpacity.toString(),
  );

  const strokeWidthInputRef = useRef<HTMLInputElement | null>(null);

  // Synchronize local state when selectedPath changes
  useEffect(() => {
    setLocalStrokeWidth(selectedPath.strokeWidth.toString());
  }, [selectedPath.strokeWidth]);

  useEffect(() => {
    setLocalFillOpacity(selectedPath.fillOpacity.toString());
  }, [selectedPath.fillOpacity]);

  useEffect(() => {
    setLocalStrokeOpacity(selectedPath.strokeOpacity.toString());
  }, [selectedPath.strokeOpacity]);

  return (
    <div
      style={{
        userSelect: 'none',
        fontSize: '12px',
        color: 'white',
        width: '100%',
        maxWidth: '100%',
        boxSizing: 'border-box',
        overflow: 'hidden',
      }}
    >
      {/* Fill Color Picker */}
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          marginBottom: '10px',
          gap: '10px',
        }}
      >
        <label style={{ whiteSpace: 'nowrap' }}>Fill Color:</label>
        <input
          type="color"
          value={selectedPath.fill}
          onChange={(e) => artboardViewModel.updateFillColor(e.target.value)}
          style={{
            flex: 1,
            height: '30px',
            border: 'none',
            padding: 0,
            boxSizing: 'border-box',
          }}
        />
      </div>

      {/* Stroke Color Picker */}
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          marginBottom: '10px',
          gap: '10px',
        }}
      >
        <label style={{ whiteSpace: 'nowrap' }}>Stroke Color:</label>
        <input
          type="color"
          value={selectedPath.strokeColor || '#000000'}
          onChange={(e) => artboardViewModel.updateStrokeColor(e.target.value)}
          style={{
            flex: 1,
            height: '30px',
            border: 'none',
            padding: 0,
            boxSizing: 'border-box',
          }}
        />
      </div>

      {/* Stroke Width */}
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          marginBottom: '10px',
          gap: '10px',
        }}
      >
        <label style={{ whiteSpace: 'nowrap' }}>Stroke Width:</label>
        <input
          ref={strokeWidthInputRef}
          type="number"
          value={localStrokeWidth}
          onChange={(e) => {
            setLocalStrokeWidth(e.target.value);
          }}
          onKeyDown={(e) => {
            if (e.key === 'Enter') {
              let value = parseFloat(localStrokeWidth);
              if (isNaN(value)) {
                value = selectedPath.strokeWidth;
                setLocalStrokeWidth(value.toString());
              }
              strokeWidthInputRef.current?.blur();
              artboardViewModel.updateStrokeWidth(value);
            }
          }}
          min={0}
          step={1}
          style={{
            flex: 1,
            padding: '5px',
            boxSizing: 'border-box',
          }}
        />
      </div>

      {/* Cap Type */}
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          marginBottom: '10px',
          gap: '10px',
        }}
      >
        <label style={{ whiteSpace: 'nowrap' }}>Cap Type:</label>
        <div style={{ display: 'flex', gap: '5px', flex: 1 }}>
          <button
            onClick={() => artboardViewModel.updateCapType(CapType.BUTT)}
            style={{
              flex: 1,
              padding: '5px',
              fontSize: '12px',
              textAlign: 'center',
            }}
          >
            B
          </button>
          <button
            onClick={() => artboardViewModel.updateCapType(CapType.ROUND)}
            style={{
              flex: 1,
              padding: '5px',
              fontSize: '12px',
              textAlign: 'center',
            }}
          >
            R
          </button>
          <button
            onClick={() => artboardViewModel.updateCapType(CapType.SQUARE)}
            style={{
              flex: 1,
              padding: '5px',
              fontSize: '12px',
              textAlign: 'center',
            }}
          >
            S
          </button>
        </div>
      </div>

      {/* Corner Type */}
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          marginBottom: '10px',
          gap: '10px',
        }}
      >
        <label style={{ whiteSpace: 'nowrap' }}>Corner Type:</label>
        <div style={{ display: 'flex', gap: '5px', flex: 1 }}>
          <button
            onClick={() => artboardViewModel.updateCornerType(CornerType.MITER)}
            style={{
              flex: 1,
              padding: '5px',
              fontSize: '12px',
              textAlign: 'center',
            }}
          >
            M
          </button>
          <button
            onClick={() => artboardViewModel.updateCornerType(CornerType.ROUND)}
            style={{
              flex: 1,
              padding: '5px',
              fontSize: '12px',
              textAlign: 'center',
            }}
          >
            R
          </button>
          <button
            onClick={() => artboardViewModel.updateCornerType(CornerType.BEVEL)}
            style={{
              flex: 1,
              padding: '5px',
              fontSize: '12px',
              textAlign: 'center',
            }}
          >
            B
          </button>
        </div>
      </div>

      {/* Fill Opacity */}
      <div style={{ marginBottom: '10px' }}>
        <label style={{ display: 'block', marginBottom: '5px' }}>Fill Opacity:</label>
        <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
          <input
            type="range"
            min="0"
            max="1"
            step="0.01"
            value={localFillOpacity}
            onChange={(e) => {
              setLocalFillOpacity(e.target.value);
            }}
            onMouseUp={() => {
              let value = parseFloat(localFillOpacity);
              value = isNaN(value) ? 1 : value;
              artboardViewModel.updateFillOpacity(value);
            }}
            style={{ flex: 1, boxSizing: 'border-box' }}
          />
          <input
            type="number"
            min="0"
            max="1"
            step="0.01"
            value={localFillOpacity}
            onChange={(e) => {
              setLocalFillOpacity(e.target.value);
            }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                let value = parseFloat(localFillOpacity);
                value = isNaN(value) ? 1 : value;
                artboardViewModel.updateFillOpacity(value);
              }
            }}
            style={{
              width: '50px',
              padding: '2px',
              textAlign: 'center',
              boxSizing: 'border-box',
            }}
          />
        </div>
      </div>

      {/* Stroke Opacity */}
      <div style={{ marginBottom: '10px' }}>
        <label style={{ display: 'block', marginBottom: '5px' }}>Stroke Opacity:</label>
        <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
          <input
            type="range"
            min="0"
            max="1"
            step="0.01"
            value={localStrokeOpacity}
            onChange={(e) => {
              setLocalStrokeOpacity(e.target.value);
            }}
            onMouseUp={() => {
              let value = parseFloat(localStrokeOpacity);
              value = isNaN(value) ? 1 : value;
              artboardViewModel.updateStrokeOpacity(value);
            }}
            style={{ flex: 1, boxSizing: 'border-box' }}
          />
          <input
            type="number"
            min="0"
            max="1"
            step="0.01"
            value={localStrokeOpacity}
            onChange={(e) => {
              setLocalStrokeOpacity(e.target.value);
            }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                let value = parseFloat(localStrokeOpacity);
                value = isNaN(value) ? 1 : value;
                artboardViewModel.updateStrokeOpacity(value);
              }
            }}
            style={{
              width: '50px',
              padding: '2px',
              textAlign: 'center',
              boxSizing: 'border-box',
            }}
          />
        </div>
      </div>
    </div>
  );
};

export default PathProperties;
