import React from 'react';
import { ArtboardViewModel } from '../../viewmodel/ArtboardViewModel';
import { ArtboardObject } from '../../lib/ArtboardObject';

interface ObjectPropertiesProps {
  artboardViewModel: ArtboardViewModel;
  selectedObject: ArtboardObject;
}

const ObjectProperties: React.FC<ObjectPropertiesProps> = ({
  artboardViewModel,
  selectedObject,
}) => {
  return (
    <div
      style={{
        userSelect: 'none',
        fontSize: '12px',
        color: 'white',
        width: '100%', // Ensure it fits the container
        maxWidth: '100%', // Prevent any overflow
        boxSizing: 'border-box',
        overflow: 'hidden',
      }}
    >
      {/* Move Up and Move Down Buttons */}
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignContent: 'center',
          marginBottom: '10px',
          gap: '5px',
        }}
      >
        Z-Index
        <button
          onClick={artboardViewModel.moveObjectUp}
          style={{
            background: 'none',
            color: 'white',
            fontSize: '16px',
            cursor: 'pointer',
            padding: '5px',
            width: '32px',
            height: '32px',
            boxSizing: 'border-box',
          }}
        >
          ▲
        </button>
        <button
          onClick={artboardViewModel.moveObjectDown}
          style={{
            background: 'none',
            color: 'white',
            fontSize: '16px',
            cursor: 'pointer',
            padding: '5px',
            width: '32px',
            height: '32px',
            boxSizing: 'border-box',
          }}
        >
          ▼
        </button>
      </div>

      {/* Opacity */}
      <div style={{ marginBottom: '10px' }}>
        <label
          style={{
            display: 'block',
            marginBottom: '5px',
            whiteSpace: 'nowrap',
          }}
        >
          Opacity:
        </label>
        <div
          style={{
            display: 'flex',
            alignItems: 'center',
            gap: '10px',
            maxWidth: '100%',
          }}
        >
          <input
            type="range"
            min="0"
            max="1"
            step="0.01"
            value={selectedObject.opacity}
            onChange={(e) => artboardViewModel.updateOpacity(parseFloat(e.target.value))}
            style={{
              flex: 1,
              maxWidth: 'calc(100% - 60px)', // Ensure space for numeric input
              boxSizing: 'border-box',
            }}
          />
          <input
            type="number"
            min="0"
            max="1"
            step="0.01"
            value={selectedObject.opacity}
            onChange={(e) => artboardViewModel.updateOpacity(parseFloat(e.target.value))}
            style={{
              width: '50px',
              padding: '2px',
              textAlign: 'center',
              boxSizing: 'border-box',
            }}
          />
        </div>
      </div>
    </div>
  );
};

export default ObjectProperties;
